@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800;900&display=swap);
* {
  margin: 0;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  background-color: #120b35;
}

a {
  text-decoration: none;
  color: inherit;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

html::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 0;
  background-color: transparent;
}

html::-webkit-scrollbar {
  width: 8px;
  background-color: transparent;
}

html::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #f0c38e;
}

.effect-wrap .effect{position:absolute;z-index:0}.square{width:24px;height:24px;border:4px solid #8a49ff;right:10%;bottom:10%;animation:spin 10s linear infinite}.grid{left:3%;bottom:20%;width:40px;display:flex;flex-wrap:wrap;justify-content:space-between;animation:topBounce 3s ease-in-out infinite}.grid div{height:3px;width:3px;background-color:#ff9c07;margin:0 3px 8px}.semi-circle{width:180px;height:180px;border:24px solid #ff9c07;border-radius:50%;left:50%;top:-130px;animation:leftBounce 3s ease-in-out infinite}.react-logo{left:15%;top:20%;animation:spin 30s linear infinite}.react-logo img{width:100px}.triangle{border-top:30px solid transparent;border-left:30px solid #06d79c;width:10px;bottom:20%;left:40%;animation:spin 15s linear infinite}.triangle::before{content:'';border-top:30px solid transparent;border-left:30px solid #06d79c;position:absolute;opacity:0.5;left:-35px;top:-25px}.circle{height:50px;width:50px;right:10%;top:30%;display:flex;justify-content:space-between;border-radius:50%;overflow:hidden;animation:spin 10s linear infinite;z-index:-10}.circle div{width:1px;background-color:#4dd0e1}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes topBounce{0%,100%{transform:translateY(0)}50%{transform:translateY(25px)}}@keyframes leftBounce{0%,100%{transform:translateX(0)}50%{transform:translateX(25px)}}@keyframes fadeInTop{0%{opacity:0;transform:translateY(-25px)}100%{opacity:1;transform:translateY(0px)}}

.title{width:100%;height:100px;display:flex;justify-content:center;align-items:center;margin:30px 0 20px;text-transform:uppercase}.title .title__container{width:100%;text-align:center}

.homeBtn {
  position: absolute !important;
  top: 30px;
  left: 30px;
}

.menuBtn {
  position: absolute !important;
  top: 30px;
  right: 30px;
}

